import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule }   from '@angular/forms';
import { RouterModule } from "@angular/router";

import { AppComponent } from './components/app.component';
import { MenuComponent } from './components/menu.component';
import { NewUserComponent } from './components/new-user.component';
import { ExistingUserComponent} from "./components/existing-user.component";
import { TestDetailComponent } from './components/test-detail.component';
import { LoginComponent } from './components/login.component';

@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    RouterModule.forRoot([
      {
        path: 'new-user',
        component: NewUserComponent
      },
      {
        path: 'existing-user/:id',
        component: ExistingUserComponent
      },
      {
        path: 'login',
        component: LoginComponent
      },
      {
        path: '',
        redirectTo: 'home',
        pathMatch: 'full'
      },
      {
        path: 'home',
        component: MenuComponent
      }
    ])
  ],
  declarations: [
    AppComponent,
    MenuComponent,
    NewUserComponent,
    ExistingUserComponent,
    TestDetailComponent,
    LoginComponent
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



