import { Component } from '@angular/core';
import { isUndefined } from "util";
import * as cookie from 'cookie';

@Component({
  selector: 'login',
  templateUrl: '../templates/login.component.html'
})


export class LoginComponent {
  displayLoginMenu = false;
  id = ""

  ngOnInit(): void {
    this.displayLoginMenu = false;
    var cookies = cookie.parse(document.cookie);
    if(!isUndefined(cookies['id'])) {
      location.replace('/existing-user/' + cookies['id']);
    } else {
      this.displayLoginMenu = true;
    }
  }

  login() {
    if(this.id !== "") {
      document.cookie = cookie.serialize('id', this.id);
      location.replace('/existing-user/' + this.id);
    }
  }
}



