import * as cookie from 'cookie';
import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-test-detail',
  templateUrl: '../templates/test-detail.component.html'
})
export class TestDetailComponent implements OnInit {
  @Input() testStatus: Object;
  @Input() id: string;

  constructor() {
  }

  ngOnInit() {
  }

  clickTest(testStatus) {
    var queryString = ""
    if (testStatus.hasQueryString) {
      queryString = testStatus.queryString + this.id
    }
    const finalUrl = testStatus.Url + queryString
    window.open(finalUrl,'_blank');
    testStatus.Completed = true;
    document.cookie = cookie.serialize(testStatus.Name, true);
  }
}


