import { Component } from '@angular/core';
import { isUndefined } from "util";
import * as cookie from 'cookie';
import * as uuid from 'uuid';
import { Location, LocationStrategy, PathLocationStrategy} from "@angular/common";

@Component({
  selector: 'new-user',
  providers: [Location, {provide: LocationStrategy, useClass: PathLocationStrategy}],
  templateUrl: '../templates/new-user.component.html'
})


export class NewUserComponent {
  displayWelcomeMessage = false;
  id = uuid();

  ngOnInit(): void {
    this.displayWelcomeMessage = false;
    var cookies = cookie.parse(document.cookie);
    if(!isUndefined(cookies['id'])) {
      location.replace('/existing-user/' + cookies['id']);
    } else {
      this.displayWelcomeMessage = true;
      document.cookie = cookie.serialize('id', this.id);
    }
  }
}



